
import store from '@/store';
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'NavigationBar',
  props: {
    type: String,
  },
  data() {
    return {
      links: store.getters.getLINKS,
    };
  },
  computed: {
    styles() {
      if (this.type === 'header') {
        return ['navbar_header'];
      }
      if (this.type === 'footer') {
        return ['navbar_footer'];
      }
      return 'style-class-none';
    },
  },
});
